import React from 'react';
import './output.css';
export const Output = (props) => {
    // console.log(props.output);
    return <div id="images">
        <ul>{props.output.map(ele =>
            <li key={ele.id}><img src={ele.images.original.url} alt='Not found'/></li>)}
        </ul>
    </div>
}
