import axios from 'axios';
export const instance = axios.create({
    baseURL: 'https://api.giphy.com/v1/gifs/',
    timeout: 5000,				// Taking response time otherwise crash it (1sec = 1000)
    // headers: {'Api_key': 'dfsdd5'}
});

export const instance1 = axios.create({
    baseURL: 'http://cricapi.com/api/',
    timeout: 5000,
});

instance.interceptors.response.use(function (response) {
    console.log('Interceptor response called',response);
    return response;
  }, function (error) {
    return Promise.reject(error);
  });


instance.interceptors.request.use(function (req) {
    console.log('Interceptor request called',req);
    return req;
  }, function (error) {
    return Promise.reject(error);
  });


axios.defaults.baseURL = 'http://api.giphy.com/v1/gifs/';
